@use "../globals" as *;
.slider {
  top: 5rem;
  margin: 4rem 0 4rem;
  height: 40rem;
  display: flex;
  flex-direction: column;
  z-index: 2;
  position: relative;
  &__btn__stats {
    border: none;
    display: flex;
    border-radius: 5rem;
    width: 6%;
    font: inherit;
    color: $txt-color-secondary;
    font-weight: 600;
    padding: 1.2rem 4.6rem;
    justify-content: center;
    font-size: $h4-size;
    background: $bg-color-pokemontable;
    white-space: nowrap;
    gap: 2px;
    align-items: center;
    &__container {
      display: inline;
    }

    span:first-child {
      font-size: $p-size;
    }

    &__left {
      position: absolute;
      top: -15%;
      left: 6%;
    }
    &__right {
      position: absolute;
      top: -15%;
      right: 6%;
    }
  }

  &__image {
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    height: 100%;
    opacity: 0;
    z-index: 1;
    transition: background-Image 0.5s ease-in-out, opacity 1s;
  }
  &__image::selection {
    color: rgb(0, 0, 0);
  }
  ion-icon {
    height: 5rem;
    width: 5rem;
    cursor: pointer;
  }
  &__arrow__left {
    position: absolute;
    top: 50%;
  }
  &__arrow__right {
    position: absolute;
    top: 50%;
    right: 0;
  }
  &__titles {
    display: flex;
    flex-direction: column;
  }

  &__dot {
    display: flex;
    gap: 1rem;
    justify-content: center;
    button {
      width: 2rem;
      height: 2rem;
      border-radius: 50%;
      background-color: $bg-color-dot;
      border: none;
      transition: background-color 0.5s ease-in-out;
      cursor: pointer;
    }
    button:hover,
    button:active {
      background-color: $bg-color-secondary;
    }
    button[data-active="true"] {
      background-color: $bg-color;
    }
    button[data-active="true"]:hover {
      background-color: $bg-color-secondary;
    }
  }
}
